.contacts {
    background-image: url(../img/footer_background.webp);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 80px 0 80px;

    .contacts__inner {
        display: flex;
        flex-direction: column;
        color: $white;
        text-align: center;

        .contacts__block {
           
            .contacts__title {
                font-size: 26px;
                font-weight: 500;
            }

            .contacts__list {
                padding-top: 25px;
                
                & > li {
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 29px;
                }
            }
        }

        .contacts__extra-info {
            padding-top: 80px;
            font-size: 18px;
            font-weight: 500;

            .contacts__used-tech {
                max-width: 700px;
                margin: 0 auto;
                line-height: 26px;
            }

            .contacts__extra-info-below {
                font-size: 15px;
                font-weight: 400;
                color: #cccccc;
                padding-top: 5px;
            }
        }

        .contacts__old-pf {
            padding-top: 50px;

            @include button-default(contacts, 340px, 45px)
        }
    }
}

@media (max-width: 780px) {
    .contacts {
        background: $black;
    }
}

@media (max-width: 380px) {
    .contacts .contacts__inner .contacts__old-pf {
        @include button-default(contacts, 250px, 60px) {
            font-size: 12px;
            cursor: pointer;
            font-size: 20px;
            
            &::before {
                content: '';
            }
        }
    }
} 