.profile {
    background-image: url(../img/profile_background.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
}

.profile__skills {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.profile__title {
    font-size: 32px;
    font-weight: 500;
    color: $black;
    margin-top: 50px;
    width: 100%;
    padding-bottom: 10px;
    border-bottom: 5px solid $black;
    margin-bottom: 45px;
}

.profile__info-block {
    width: 592px;
    padding: 0 8px 40px;

    .profile__subtitle {
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
        color: $black;
        margin-bottom: 18px;
    }

    .profile__skill-list > li {
        font-size: 16px;
        color: #656565;
        padding-left: 30px;
        position: relative;

        &::before {
            font-family: "Font Awesome 5 Free";
            font-weight: 600;
            font-size: 12px; 
            content: "\f00c";
            position: absolute;
            left: 12px;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    .profile__single-subtitle {
        @extend .profile__subtitle;
        margin-top: 15px;
        font-size: 18px;
    }
}

@media (max-width: 1790px) {
    .profile__info-block {
        width: 522px;
    }
}

@media (max-width: 1590px) {
    .profile__info-block {
        width: 452px;
    }
}

@media (max-width: 1430px) {
    .profile__info-block {
        width: 402px;
    }
}

@media (max-width: 1290px) {
    .profile__info-block {
        width: 592px;
    }

    .profile__info-block_1290px:not(:last-child) {
        padding: 0 8px;
    }
    
    .profile__info-block_1290px:last-of-type 
    .profile__single-subtitle:first-of-type {
        margin-top: 0;
    }
}