.portfolio {
    overflow: hidden;
}

.portfolio__title-block {    
    @include d-flex(row);
    align-items: center;

    height: 125px;
    background-image: url(../img/portfolio-header_background.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    .portfolio__title {
        color: $white;
        font-size: 26px;
        font-weight: 500;
    }
}

.moveup {
    transform: translateY(-600px);
    visibility: hidden;
    opacity: 0;
}

.portfolio__project-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .portfolio__project-link {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        transition: all 0.1s;

        .portfolio__project-block {
            @include d-flex(column);

            position: absolute;
            bottom: 0;
            width: 100%;
            height: 38%;
            align-items: center;
            transform: translateY(110px);
            opacity: 0;
            background-color: rgba(0, 0, 0, 0.8);
            text-align: center;
            transition: all 0.4s;

            .portfolio__project-text {
                max-width: 500px;
                font-size: 16px;
                font-weight: 400;
                color: $white;
            }

            .portfolio__button-group {
                display: flex;
                flex-direction: row;
                justify-content: center;
            }
        }

        &:hover {            
            background-color: rgba(0, 0, 0, 0.3);
        }

        &:hover .portfolio__project-block {
            opacity: 1;
            transform: translate(0px)
        }
    }

    @include project-back(carsshop, 50%);
    @include project-back(enigma, 50%);
    @include project-back(complex-zp, 50%);
    @include project-back(zoon, 50%);
    @include project-back(photo-studio, 50%);
    @include project-back(goldcoach, 50%);
    @include project-back(property1, 50%);
    @include project-back(mate, 50%);
    @include project-back(mgid, 50%);
    @include project-back(resort, 50%);
}

.portfolio__project-button {
    margin-top: 8px;

    @include button-default(portfolio, 250px, 30px) {
        font-size: 12px;
        cursor: pointer;
    }
}

.portfolio__project-button_smaller {
    margin-top: 8px;

    @include button-default(portfolio, 120px, 30px) {
        font-size: 12px;
        cursor: pointer;
        margin-right: 5px;

        &::before {
            content: '';
        }
    }
}

@media (max-width: 1390px) {
    .portfolio__project-list {
        @include project-back(carsshop, 100%);
        @include project-back(enigma, 100%);
        @include project-back(complex-zp, 100%);
        @include project-back(zoon, 100%);
        @include project-back(photo-studio, 100%);
        @include project-back(goldcoach, 100%);
        @include project-back(property1, 100%);
        @include project-back(mate, 100%);
        @include project-back(mgid, 100%);
        @include project-back(resort, 100%);
    }
}

@media (max-width: 780px) {
    .portfolio__title-block {
        background: $black;
    }
    .portfolio__project-list 
    .portfolio__project-link 
    .portfolio__project-block {
        transform: none;
        opacity: 1;
        background-color: rgba(0, 0, 0, 0.92);
    }
}

@media (max-width: 510px) {
    .portfolio__project-list 
    .portfolio__project-link 
    .portfolio__project-block 
    .portfolio__project-text {
        display: none;
    }

    .portfolio__project-button {
        margin-top: 0;
    
        @include button-default(portfolio, 250px, 60px) {
            font-size: 12px;
            cursor: pointer;
            font-size: 20px;
            
            &::before {
                content: '';
            }
        }
    }

    .portfolio__project-button_smaller {
        display: none;
    }
}