.profile__expirience {
    @extend .profile__skills;

    .profile__title {
        margin-top: 0;
    }

    .profile__data-list {
        font-size: 18px;
        font-weight: 500;

        .profile__inner {
            max-width: 450px;
        }

        .profile__work-list {
            margin-top: 10px;

