.header-wrap {
    position: relative;
}

.header {
    height: 783px;
    padding-top: 174px;
    position: relative;
    overflow-x: hidden;
    
    .header__menu {
        margin: 0 auto;
    }
}

@media (max-width: 1080px) {
    .header {
        padding-top: 144px;
    }
}

@media (max-width: 520px) {
    .header {
        height: 683px
    }
}

@media (max-width: 380px) {
    .header {
        height: 380px;
    }
}