.about-me__info-block {
    @include d-flex(column);

    margin-top: 75px;
    text-align: center;

    .about-me__title {
        font-size: 32px;
        font-weight: 500;
        color: $white;
        text-transform: uppercase;
    }

    .about-me__text {
        font-size: 17px;
        line-height: 30px;
        color: #eee;
        max-width: 800px;
        margin: 14px auto;
    }
}

@media (max-width: 780px) {
    .about-me__info-block {
        margin-top: 35px;
    }
}

@media (max-width: 640px) {
    .about-me .container {
        width: 100%;
    }
}

@media (max-width: 520px) {
    .about-me__info-block {
        .about-me__title {
            font-size: 26px;
        }
        .about-me__text {
            font-size: 15px;
        }
    }
}

@media (max-width: 380px) {
    .about-me__info-block {
        margin-top: 15px;
    }
}