@mixin full-parent-pos() {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; 
}

@mixin shadow-bg-animate($color, $dev) {
    @keyframes shadow-bg-#{$dev} {
        from {
            background: transparent;
            transform: scale(1)
        }
        to {
            background: $color;
            transform: scale(1.1)
        }
    }
}

@mixin d-flex($direct) {
    display: flex;
    justify-content: center;
    flex-direction: $direct;
}

@mixin project-back($name, $width) {
    .project-item-#{$name} {
        background-image: url(../img/#{$name}.webp);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center top;
        width: $width;
        height: 562px;
        transition: 1.2s;
    }
}

@mixin button-default($section, $width, $height) {
    .#{$section}__button-default {
        display: inline-block;
        width: $width;
        height: $height;
        border: 2px solid $white;
        border-radius: 11px;
        transition: all 0.2s;
        position: relative;
        transition: all 0.2s;

        .#{$section}__button-link {
            font-size: 17px;
            @content;
            font-weight: 500;
            line-height: $height - 2px;
            color: $white;
            width: 100%;
            height: 100%;
            transition: all 0.2s;
        }

        &::before {
            font-family: "Font Awesome 5 Free";
            font-weight: 600;
            font-size: 15px; 
            content: '\f101';
            color: $white;
            position: absolute;
            top: 50%;
            right: 33%;
            transform: translateY(-50%);
            transition: all 0.2s;
        }

        &:hover {
            background-color: $white;
        }
        &:hover .#{$section}__button-link {
            color: $black;
        }
        &:hover::before {
            color: $black;
        }
        &:active {
            transform: scale(0.97)
        }
        @content;
    }
}