.menu {
    width: 721px;
    height: 58px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 4px solid $black;

    .header__item {
                    
        & + .header__item {
            margin-left: 18px;
        }
    }
    .header__link {
        color: $black;
        font-size: 18px;
        font-weight: 600;
        text-transform: uppercase;
        padding: 5px;
        position: relative;
        z-index: 50;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            height: 0%;
            z-index: -1;
            width: 100%;
            background: $black;
            transition: height .15s ease-out;
        }

        &:hover::before {
            height: 100%;
        }
            
        &:hover {
            color: $white;
        }
    }
}

@media (max-width: 1080px) {
    .menu {
        flex-direction: column;
        background: none;
        border: none;
        box-shadow: none;
        width: auto;

        .header__item + .header__item {
            margin-left: 0;
            margin-top: 8px;
        }
    }
}