.header__photo-container {
    @include full-parent-pos();
}

.header__photo {
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: 100;
    transform: translate(-50%, 44px);
    width: 471px;
    height: 484px;
}

@media (max-width: 520px) {
    .header__photo {
        width: 350px;
        height: auto;
    }

    .header__photo {
        transform: translate(-50%, 33px);
    }
}

@media (max-width: 380px) {
    .header__photo {
        display: none;
    }
}