.about-me__social-icons {
    padding-top: 56px;

    .about-me__social-group {
        @include d-flex(row);

        .about-me__item + .about-me__item {
            margin-left: 23px;
        }
    
        .about-me__link {
            color: #a9a9a9;
            font-size: 35px;
            transition: 0.2s;

            &:hover {
                transform: translateY(-2px);
                color: $white;
            }
        }
    }
}

.delay-2_3s {
    animation-delay: 2.3s
}
.delay-2_6s {
    animation-delay: 2.6s
}
.delay-2_9s {
    animation-delay: 2.9s
}
.delay-3_2s {
    animation-delay: 3.2s
}

@media (max-width: 380px) {
    .about-me__social-icons {
        padding-top: 26px;
    }
}