.circle-wave {
    position: absolute;
    transform: translate(-50%, -50%);
    display: inline-block;
    background: #eaeaea;
    border-radius: 50%;
    animation: wave 0.45s linear infinite
  }
  
  @keyframes wave {
    from {
      width: 0px;
      height: 0px;
      opacity: 1;
    }
    to {
      width: 80px;
      height: 80px;
      opacity: 0
    }
  }