.header__icons-group {
    @include full-parent-pos();
    
    .header__block {
        width: 100%;
    }

    .header__icon-img {
        position: absolute;
        border-radius: 8px;
        box-shadow: -15px 15px 10px 1px rgba(0, 0, 0, 0.35)
    }

    transition: 1s;
}

.hide-icons {
    opacity: 0;
    visibility: hidden;
    top: -1000px;
}

.icon-img_js {
    top: 67px;
    left: 7%;
    height: 120px;
    width: 120px;
    
    animation: shadow-bg-js 1.75s infinite alternate;
}

.icon-img_ng {
    top: 141px;
    left: 85.6%;
    height: 110px;
    width: 110px;

    animation: shadow-bg-ng 1.75s infinite alternate;
}

.icon-img_html {
    top: 336px;
    left: 31.8%;
    height: 70px;
    width: 70px;

    animation: shadow-bg-html 1.75s infinite alternate;
}

.icon-img_css {
    top: 483px;
    left: 64.6%;
    height: 80px;
    width: 80px;

    animation: shadow-bg-css 1.75s infinite alternate;
}

.icon-img_bt {
    top: 545px;
    left: 19.9%;
    height: 85px;
    width: 85px;
    
    animation: shadow-bg-bt 1.75s infinite alternate;
}

.icon-img_gulp {
    top: 621px;
    left: 87.8%;
    height: 60px;
    width: 60px;

    animation: shadow-bg-gulp 1.75s infinite alternate;
}

@include shadow-bg-animate(#f7df1e, js);
@include shadow-bg-animate(#dd0031, ng);
@include shadow-bg-animate(#f16529, html);
@include shadow-bg-animate(#379ad6, css);
@include shadow-bg-animate(#563e7c, bt);
@include shadow-bg-animate(#eb4a4b, gulp);

@media (max-width: 1260px) {
    .header__icon-img {
        display: none;
    }
}