.button-up {
    position: fixed;
    bottom: 100px;
    right: 5%;
    z-index: 99;
    width: 50px;
    height: 50px;
    background-color: #757575;
    border-radius: 25%;
    transition: all 0.33s;
    opacity: 0;
    visibility: hidden;

    .button-up__link {
        position: relative;
        width: 100%;
        height: 100%;
        border-radius: 25%;

        .button-up__icon {
            font-size: 25px;
            color: $black;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &.show {
        opacity: 0.75;
        visibility: visible;
    }

    &:hover {
        background-color: lighten(#757575, 20%);
    }

    &:active {
        transform: scale(0.97)
    }
}

.display-n {
    display: none;
    opacity: 0;
}